import React,{useEffect} from 'react';

import Api from '../config/Api';

const Dashboard = ()=>{
    
    const getMyJobs=()=>{
        Api.getMyJobs((data)=>{
            if(data.status){
                console.log(data);
            }else{
                console.error("Failed to load My Jobs");
            }
        });
    };
    useEffect(()=>{
        getMyJobs();
    },[]);
    return (
        <div>
            <h3>Dashboard</h3>
            This is dashboard
        </div>
    );
}

export default Dashboard;